"use client";

import { useState } from "react";
import { useSearchParams, useRouter } from "next/navigation";
import { api } from "@/lib/api";
import { toast } from "react-hot-toast";
import { Package, Send, AlertCircle } from "lucide-react";

export default function CreateRFQPage() {
  const searchParams = useSearchParams();
  const router = useRouter();
  const productId = searchParams.get("productId"); // We pass this via URL
  
  const [loading, setLoading] = useState(false);
  const [form, setForm] = useState({ quantity: 0, message: "" });

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    try {
      await api.post("/rfq", { productId, ...form });
      toast.success("Request sent to vendor!");
      router.push("/buyer/rfq/list");
    } catch (err: any) {
      toast.error(err.response?.data?.message || "Failed to send request");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-2xl mx-auto p-10">
      <div className="bg-white rounded-3xl p-8 border border-slate-200 shadow-sm">
        <h1 className="text-2xl font-black mb-6 flex items-center gap-2">
          <Package className="text-blue-600" /> Create Bulk Request
        </h1>

        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <label className="block text-sm font-bold text-slate-700 mb-2">Quantity Required</label>
            <input 
              type="number" 
              required
              className="w-full p-4 bg-slate-50 border border-slate-200 rounded-2xl focus:ring-2 focus:ring-blue-500 outline-none"
              placeholder="e.g. 500"
              onChange={(e) => setForm({...form, quantity: Number(e.target.value)})}
            />
          </div>

          <div>
            <label className="block text-sm font-bold text-slate-700 mb-2">Message to Vendor</label>
            <textarea 
              rows={4}
              className="w-full p-4 bg-slate-50 border border-slate-200 rounded-2xl focus:ring-2 focus:ring-blue-500 outline-none"
              placeholder="Ask about shipping, customization, or lead times..."
              onChange={(e) => setForm({...form, message: e.target.value})}
            />
          </div>

          <button 
            disabled={loading}
            className="w-full py-4 bg-blue-600 text-white font-bold rounded-2xl hover:bg-blue-700 transition-all flex items-center justify-center gap-2 disabled:opacity-50"
          >
            {loading ? "Sending..." : "Send Request"} <Send size={18} />
          </button>
        </form>
      </div>
    </div>
  );
}